



































































































































































































































































































































































































.over-bg {
    width: calc(100% - 40px);
    padding: 20px;
    background: #e8ebf2;
}
.overContent {
    background: white;
    height: calc(100vh - 100px);
    overflow: auto;
}
.over-box {
    padding: 65px 269px 20px 165px;
}
.over-box > .el-input {
    max-width: 840px;
    margin-bottom: 10px;
}
.ul-title {
    font-size: 18px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #333333;
    line-height: 25px;
    margin-bottom: 22px;
}
.ul-box {
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.ul-list {
    width: 100%;
    padding: 20px 80px;
}
ul {
    li {
        cursor: pointer;
        font-size: 14px;
        display: inline-block;
        width: 150px;
        line-height: 30px;
        a {
            text-decoration: none;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #999999;
        }
    }
}
